.display_text__multiplayerMenu {
  max-width: 300px;
  height: 60px;
  background-color: black;
  margin: 0 auto;
  padding: 40px 20px; }

#mpName {
  width: 80%;
  height: 30px;
  border: solid 1px;
  border-radius: 5px;
  padding: 0px 6px;
  border-color: white; }

#mpColor {
  width: 40%;
  height: 30px;
  margin: 10px 10px 10px 0; }

#mpSubmit {
  width: 35%;
  height: 30px;
  border: solid 1px;
  border-radius: 5px;
  background-color: white;
  border-color: white; }

#resetGameButton {
  width: 100px;
  height: 60px;
  float: left;
  background-color: black;
  margin: 10px;
  padding: 10px; }

#resetGameButton:hover {
  cursor: pointer; }
